#selectlang-container {
  display: flex;
}

#selectlang-title {
  display: flex;
  justify-content: space-between;
}

#selectlang-title > #reset {
  font-weighft: bold;
  cursor: pointer;
  border-radius: 100%;
  border-width: 0;
  width: 1.5em;
  height: 1.5em;
  background-color: gray;
  display: flex;
  justify-content: center;
}

#selectlang-title > #reset > span {
  transform: translateY(-0.08em);
}

div#custom-container {
  width: 70%;
  position: fixed;
  left: 15%;
}

@media screen and (max-width: 650px) {
  div#custom-container {
    left: 0;
    width: 100%;
  }
}
